.feedPage .highlights {
  padding: 15px;
  margin: 0;
  background: #fff;
  box-shadow: 0 0 4px #00000026;
  border: 0;
  border-radius: 6px;
  font-size: 0.75rem; }

.feedPage .mt15 {
  margin-top: 15px; }

.feedPage .feedFilters {
  padding: 0 0;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  /*  background: #fff;
        box-shadow: 0 0 4px #00000026;
        border: 0;
        border-radius: 6px; */ }
  .feedPage .feedFilters .subtitle {
    margin: 0;
    margin-right: 20px;
    width: 100px; }
  .feedPage .feedFilters ul {
    list-style: none;
    padding-left: 0;
    display: flex;
    overflow-x: auto;
    margin: 0 10px;
    width: 100%; }
    .feedPage .feedFilters ul li {
      display: inline-block;
      margin-right: 20px; }

.feedPage .highlights .highlight-left {
  width: 40%; }

.feedPage .highlights .highlight-right {
  width: 60%; }
  .feedPage .highlights .highlight-right .highlight-title {
    /* margin-top: 10px; */ }

.feedPage .highlights .highlight-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap; }
  .feedPage .highlights .highlight-row .highlight-item {
    width: 49%;
    padding: 10px;
    box-sizing: border-box;
    /* box-shadow: 0 0 4px #0000002e; */
    margin-right: 2%;
    height: 250px;
    position: relative;
    border: 1px solid #E6E6E6; }
    .feedPage .highlights .highlight-row .highlight-item:last-child {
      margin-right: 0; }
    .feedPage .highlights .highlight-row .highlight-item .thumb {
      width: 100%;
      height: 100px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      border-radius: 3px; }

.feedPage .highlights .highlight-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap; }
  .feedPage .highlights .highlight-col .highlight-item {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    width: 100%;
    height: 120px;
    margin-bottom: 10px;
    box-sizing: border-box;
    box-shadow: 0 0 4px #0000002e;
    /* border-radius: 6px; */
    /* overflow: hidden; */
    padding: 10px; }
    .feedPage .highlights .highlight-col .highlight-item:last-child {
      margin-bottom: 0; }
    .feedPage .highlights .highlight-col .highlight-item .thumb {
      width: 100px;
      height: 100px;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      border-radius: 3px;
      overflow: hidden; }

.feedPage .highlights .highlight-title {
  font-weight: normal;
  font-weight: bold;
  height: 33px;
  overflow: hidden;
  font-size: 0.7rem;
  /* background: red; */
  margin: 5px 0; }

.feedPage .highlights .highlight-price {
  font-weight: bolder; }

.feedPage .highlights .highlight-category {
  font-weight: normal;
  color: #5931ea;
  font-size: 0.9rem; }

.feedPage .highlights .highlight-userinfo {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px 0 5px; }

.feedPage .highlights .highlight-profile-pic {
  width: 2rem;
  height: 2rem;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50%;
  margin-right: 10px; }

.feedPage .highlights .profile-name {
  font-size: 0.8rem; }

.feedPage .highlights .highlight-desc {
  width: calc(100% - 100px);
  padding: 0 10px;
  font-size: 14px;
  position: relative;
  height: 100px;
  box-sizing: border-box; }
  .feedPage .highlights .highlight-desc p {
    margin-bottom: 0; }

.feedPage .highlight-meta {
  /* position: absolute;
        width: 90%;
        bottom: 5px;
        left: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
        font-weight: bold; */
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  border-top: 1px solid #E9ECFA;
  /* padding-top: 4px; */
  position: absolute;
  width: 90%;
  /* bottom: 0; */
  padding: 15px 0;
  font-size: 0.75rem; }
  .feedPage .highlight-meta.justify-center {
    justify-content: center; }

.feedPage .highlight-price, .feedPage .price {
  color: #3C49F1;
  font-size: 0.7rem; }

@media screen and (max-width: 1150px) {
  .feedPage .highlights {
    display: none; } }
